@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Open+Sans:wght@600&display=swap);
*{margin:0;padding:0}*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{font-family:"Montserrat",sans-serif;color:#fff;background-color:#1b1a1f}a,input,button{font-family:"Montserrat",sans-serif;text-decoration:none;color:#fff}.app{text-align:center;min-height:100vh;display:flex;flex-direction:column;justify-content:center;padding:6rem 4rem 11.2rem;position:relative}.app__header{display:flex;align-items:center;justify-content:center;margin-bottom:4rem}.app__title{font-size:4.2rem;font-weight:300}.app__header-icon{margin-top:.6rem;margin-left:1rem;font-size:4.4rem}.app__sub-header{font-size:3.6rem;font-weight:300;margin-bottom:15.4rem}.app__footer{position:absolute;bottom:0;left:0;width:100%;height:5.2rem;display:flex;align-items:center;justify-content:center;background-color:#232228}.app__footer-title--temp{font-size:2.4rem;font-weight:300}
